import {useEffect} from "react";
import tokenList from "../utils/tokenList.json";
import axios from "axios";
import {useDispatch} from "react-redux";
import {updateTokens} from "../redux/slices/coineusSlice";

export default function useDexScreener() {

    const dispatch = useDispatch();

    useEffect(() => {
        let calls = [];
        Object.entries(tokenList.tokens).map(([chain,tokens]) => {

            Object.entries(tokens).map(([contract_address, token_info]) => {

                let call = axios
                    .get(`https://api.dexscreener.com/latest/dex/tokens/${contract_address === 'native' ? token_info.wrap : contract_address}`)
                    .then(resp => {
                        const {pairs} = resp.data;
                        token_info.pairs = pairs;
                        token_info.pair = pairs[0];
                    })
                calls.push(call);
                return [contract_address, token_info];
            })
            return [chain,tokens];
        })

        Promise.all(calls).then(() => {
            dispatch(updateTokens(tokenList.tokens));
        })
    }, [])

    return {
        tokenList
    }

}